import React from 'react';
import { Container } from '@material-ui/core';

export interface IHomeProps {
  title: string;
  description: string;
}

const Home = (props: IHomeProps) => {
  const { title } = props;
  const { description } = props;
  return (
    <Container>
      <h1>{title}</h1>
      <br />
      <p>{description}</p>
    </Container>
  );
};

export default Home;
